.banner{
    margin: 20px;
    div{
        width: 180px;
        display: flex;
        img{
            height: 60px;
            width: 60px;
        }
        h1{
            font-size: 16px;
        }
    }
}

.main-product{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    .product-title-image{
        width: 800px;
        height: 900px;
        .product-slider{
            width: 800px;
            .slider-content{
                height: 600px;
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                overflow: hidden;
                img{
                    width: 100%;
                    height: 100%;
                }
                button.next,
                button.previous{
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    i{
                        font-size: 50px;
                    }
                }
                button.next{
                    right: 30px;
                }
                button.previous{
                    left: 30px;
                }
                .pagination{
                    height: 50px;
                    width: 500px;
                    position: absolute;
                    top: 90%;
                    left: 50%;
                    transform: translateX(-50%);
                    z-index: 9;
                    .active{
                        background-color: orange !important;
                        width: 30px !important;
                    }
                    .pagination-dot{
                        background-color: black;
                        height: 10px;
                        width: 10px;
                        border-radius: 100px;
                        cursor: pointer;
                    }
                }
            }
        }
    }
    .product-customizations{
        width: 400px;
        display: flex;
        flex-direction: column;
        align-items: end;
        .product-details{
            display: flex;
            flex-direction: column;
            gap: 100px;
            .product-promotions{
                align-items: end;
                .primary-container{
                    width: fit-content;
                    button{
                        height: 25px;
                        width: 25px;
                        background-color: transparent;
                        border: 2px solid black;
                        border-radius: 100px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        i{
                            color: black;
                        }
                    }
                }
            }
            .primary-container{
                width: calc(100% - 40px);
            }
            .product-colors{
                .colors-inputs{
                    .color-input{
                        height: 30px;
                        width: 30px;
                        background-color: black;
                        border-radius: 50%;
                        cursor: pointer;
                    }
                    .silverColor{
                        background-color: #e2e4e1;
                    }
                    .goldColor{
                        background-color: #d4c9b1;
                    }
                    .DeepPurpleColor{
                        background-color: #5e5566;
                    }
                    .SpaceBlackColor{
                        background-color: #4b4845;
                    }
                    .active{
                        border: 2px solid gold;
                    }
                }
            }
        }
    }
}

.faq{
    position: relative;
    cursor: help;
    .faq-image{
        height: 100%;
        width: 40%;
        img{
            height: 100%;
            width: 100%;
            border-radius: 0;
        }
    }
    button{
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 5px;
        border-radius: 100px;
    }
}
.help{
    position: relative;
    
    button{
        position: absolute;
        top: 10px;
        right: 10px;
        border-radius: 50%;
    }
}
.customization-page{
    padding: 20px 0;
    .customization-options{
        padding: 10px;
        background-color: rgb(236, 236, 236);
        height: 90vh;
    }
}

.app {
text-align: center;
}

.main-section {
    display: flex;
    justify-content: space-around;
    padding: 20px;
}

.ingredient-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: black;
    border-radius: 9px;
    padding: 5px;
    .ingredient {
        margin: 10px;
        cursor: pointer;
        img{
            height: 80px;
            width: 80px;
        }
        p{
            color: white;
        }
    }
}

.burger {
    position: relative;
    height: fit-content;
    img {
        width: 300px;
    }
    .ingredients-layer {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        position: absolute;
        top: 0;
        right: 0%;

        img {
            height: 80px;
            width: 80px;
        }
    }
}

